html {
  --pw-text-color: black;
  --pw-bg-color: white;
  --pw-bg-edge-color: #4158d0;
  --pw-bg-edge: linear-gradient(43deg, #4158d0 0%, #c850c0 46%, #ffcc70 100%);
  --pw-button-deep-bg: linear-gradient(43deg, #4158d0 0%, #c850c0 100%);
  --pw-button-bg-shadow: linear-gradient(43deg, #4158d0 0%, #c850c0 100%);
  --pw-spacing: 1rem;
}

@media (prefers-color-scheme: dark) {
  html {
    --pw-bg-color: black;
    --pw-text-color: white;
  }
}

.dark {
  --pw-bg-color: black;
}

.light {
  --pw-bg-color: white;
}

* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  font-size: 14px;
}

header {
  width: 100%;
  background-color: var(--pw-bg-color);
  z-index: 10;
  position: sticky;
  top: 0;
}

.header-content {
  padding: var(--pw-spacing);
}

.colored-border {
  height: 5px;
  background-color: var(--pw-bg-color-edge, #4158d0);
  background-image: var(--pw-bg-edge);
}

.logo {
  width: 300px;
  transition: width .5s;
}

@media (min-width: 700px) {
  .logo {
    width: 500px;
  }

  .small {
    width: 300px;
  }

  main {
    margin-top: 110px;
  }
}

.info-code {
  padding: var(--pw-spacing);
  padding: 0;
  background-color: #000;
  grid-template-columns: 21px 1fr;
  gap: 1px;
  padding-block-start: calc(var(--pw-spacing, 1rem) * 2);
  display: grid;
}

.code-container {
  width: 100%;
  background-color: #000;
  overflow-x: auto;
}

.code {
  color: #fff;
  margin: 0;
  margin-block-end: .5rem;
  font-size: 1.5rem;
  line-height: 1.4;
  position: relative;
}

.code span {
  background-color: #000;
  padding-top: .5rem;
  padding-bottom: .5rem;
}

.line-number {
  color: #daa520;
  margin: 0;
  margin-block-end: .5rem;
  font-size: 1.5rem;
  line-height: 1.4;
  display: block;
}

.blue {
  color: #00e1ff;
}

.yellow {
  color: #ff0;
}

.green {
  color: #adff2f;
}

.pink {
  color: pink;
}

.animate {
  opacity: 0;
  animation-duration: .5s;
  animation-timing-function: ease-in;
  animation-delay: .5s;
  animation-fill-mode: forwards;
}

.leftIn {
  animation-name: leftIn;
}

.rightIn {
  animation-name: rightIn;
}

@keyframes leftIn {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes rightIn {
  from {
    opacity: 0;
    transform: translateX(100%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.typing {
  opacity: 0;
  width: 325px;
  border-right: 3px solid red;
  animation-name: typing, blink-cursor;
  animation-duration: 3s, 1s;
  animation-timing-function: steps(40, end), ease-in;
  animation-iteration-count: 1, infinite;
  animation-delay: 1s, 1s;
  animation-fill-mode: forwards, both;
  overflow: hidden;
}

@keyframes typing {
  from {
    width: 0;
    opacity: 1;
  }

  to {
    width: 325px;
    opacity: 1;
  }
}

@keyframes blink-cursor {
  from, to {
    border-color: #0000;
  }

  50% {
    border-color: red;
  }
}

.illustrations-container {
  position: relative;
}

.illustration {
  width: fit-content;
  background-color: var(--pw-bg-color-edge, #4158d0);
  background-image: var(--pw-bg-edge);
  border-radius: 50%;
  margin-block-end: var(--pw-spacing, 1rem);
  margin-inline: auto;
  padding: 3px;
}

.woman-thinking {
  border-radius: 50%;
  display: block;
}

body {
  background-color: var(--pw-bg-color);
  color: var(--pw-text-color, white);
  font-family: Courier New, Courier, monospace;
}

main {
  padding: var(--pw-spacing);
}

h1 {
  font-size: 2.5rem;
}

p {
  padding-top: 0;
}

.info {
  grid-template-columns: 1fr;
  display: grid;
  position: relative;
}

